<template>
  <v-app standalone dark>
    <section>
      <slot name="header">
        <header-section></header-section>
      </slot>
    </section>
    <v-content>
      <div class="home">
        <div class="content">
          <slot name='content'>
            <transition name="page" mode="out-in">
              <router-view></router-view>
            </transition>
          </slot>
        </div>
      </div>
    </v-content>
    <footer>
      <slot name="footer">
        <footer-section></footer-section>
      </slot>
    </footer>
  </v-app>
</template>
<script>
import { HeaderSection, FooterSection } from '.'
export default {
  components: {
    HeaderSection,
    FooterSection
  }
}
</script>



